.filter-select {
  -webkit-user-select: none;
  -moz-user-select: none;
  -ms-user-select: none;
  user-select: none;
  position: relative;
  width: 128px;
  height: 36px;
  border-radius: 3px;
  border: solid 1px #c8e8f0;
  font-size: 13px;
  cursor: pointer;
}
.filter-select--collapsed .chevron__icon {
    -webkit-transform: rotate(0);
            transform: rotate(0);
    -webkit-transition: 100ms linear;
    transition: 100ms linear;
}
.filter-select--collapsed .values {
    display: none;
}
.filter-select--expanded .chevron__icon {
    -webkit-transform: rotate(-180deg);
            transform: rotate(-180deg);
    -webkit-transition: 100ms linear;
    transition: 100ms linear;
}
.filter-select--expanded .values {
    display: block;
}
.filter-select .current-item {
    color: #036f9f;
    padding: 10px 16px 10px 16px;
}
.filter-select .chevron {
    position: absolute;
    top: 0;
    right: 0;
}
.filter-select .chevron .chevron__icon {
      position: relative;
      width: 36px;
      height: 36px;
}
.filter-select .chevron .chevron__icon::before {
        content: '';
        position: absolute;
        top: 17px;
        left: 11px;
        width: 8px;
        height: 2px;
        background-color: #000000;
        -webkit-transform: rotate(45deg);
                transform: rotate(45deg);
}
.filter-select .chevron .chevron__icon::after {
        content: '';
        position: absolute;
        top: 17px;
        left: 16px;
        width: 8px;
        height: 2px;
        background-color: #000000;
        -webkit-transform: rotate(-45deg);
                transform: rotate(-45deg);
}
.filter-select .values {
    position: absolute;
    top: 34px;
    left: 0;
    right: 0;
    padding-top: 6px;
    padding-bottom: 6px;
    background-color: #ffffff;
    border: solid 1px #c8e8f0;
}
.filter-select .values .values--item {
      padding-top: 8px;
      padding-bottom: 8px;
      padding-left: 12px;
      padding-right: 12px;
}
